* {
  font-family: Poppins Medium;
}

.Menu {
  width: 100%;
  display: flex;
  flex-direction: row;
}
.logo {
  width: 180px;
  height: 42px;
  padding-left: 73px;
  padding-top: 19px;
}
.iconsheader {
  display: flex;
}
.iconheader {
  width: 25px;
  height: 21px;
  color: #f26522;
  /* top: 21px;
    left: 207px; */
  opacity: 1;
  /* position: relative; */
  padding-right: 50px;
  padding-top: 23px;
}

nav {
  width: 100%;
  margin: 0 auto;
  background-color: white;
  position: sticky;
  top: 0px;
}

nav ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
}

nav ul li {
  /* flex: 1 1 auto; */
  text-align: center;
  position: relative;
  padding-right: 65px;
}

nav a {
  display: block;
  text-decoration: none;
  color: black;
  border-bottom: 2px solid transparent;
  font-size: 1.125rem;
  padding-bottom: 22px;
}
nav a:active {
  color: #f26522;
}

nav a:hover {
  color: #f26522;
}

.sous {
  display: none;
  box-shadow: 0px 1px 2px #ccc;
  background-color: white;
  position: absolute;
  width: 100%;
  z-index: 1;
}
nav > ul li:hover .sous {
  display: flex;
  flex-flow: column wrap;
  padding-left: 0px;
  padding-right: 0px;
}
.sous li {
  flex: 1 1 auto;
  text-align: left;
  padding-right: 0px;
}
.sous a {
  padding: 10px;
  border-bottom: none;
}
.sous a:hover {
  border-bottom: none;
}
.arrow {
  border: solid black;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 5px;
}
.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  position: absolute;
  top: 6px;
  left: 124px;
}
.categoriesmenu {
  position: relative;
}
.countshopcard {
  position: absolute;
  top: -6px;
  right: 36px;
  border: 1px solid red;
  border-radius: 13px;
  width: 20px;
  color: white;
  background: red;
}
.containerCartShopping {
  position: relative;
}
/****** responsive */
@media only screen and (max-width: 600px) {
  nav {
    display: none;
  }
  .iconsheader {
    display: none;
  }
  .Menu {
    justify-content: space-between;
    width: 351px;
    margin-top: 16px;
    margin-bottom: 16px;
    align-items: center;
  }
  .logo {
    width: 180px;
    height: 42px;
    padding-left: 0;
    padding-top: 0;
  }
}
